
#ingallery-1 {
    background: rgba(255, 255, 255, 0);
}
#ingallery-1 .ingallery-album {
    border-color: rgba(0, 185, 255, 1);
    color: rgba(0, 185, 255, 1);
    background: rgba(255, 255, 255, 1);
}
#ingallery-1 .ingallery-album.active,
#ingallery-1 .ingallery-album:hover {
    color: rgba(255, 255, 255, 1);
    background: rgba(0, 185, 255, 1);
}
#ingallery-1 .ingallery-items {
    margin-left: -7px;
    margin-right: -7px;
}
#ingallery-1 .ingallery-cell,
#ingallery-1 .ingallery-col {
    padding-left: 7px;
    padding-right: 7px;
    margin-bottom: 15px;
}
#ingallery-1 .ingallery-cell,
#ingallery-1 .ingallery-col {
    width: 20%;
}
#ingallery-1 .grid-sizer {
    width: 20%;
}
#ingallery-1 .ingallery-cell-big,
#ingallery-1 .ingallery-col-big {
    width: 40%;
}
#ingallery-1 .ingallery-item-overlay {
    background: rgba(0, 0, 0, 0.5);
}
#ingallery-1 .ingallery-item-overlay .ingallery-item-stats {
    color: rgba(255, 255, 255, 1);
}
#ingallery-1 .ingallery-loadmore-btn {
    border-color: rgba(214, 103, 205, 1);
    color: rgba(255, 255, 255, 1);
    background: rgba(214, 103, 205, 1);
}
#ingallery-1 .ingallery-loadmore-btn:hover {
    color: rgba(214, 103, 205, 1);
    background: rgba(255, 255, 255, 1);
}


